.navbar img{
    width: 10%;
    object-fit: contain;
}
.navbar{
    display: flex;
    justify-content: space-around;
    
    box-shadow: 1px 5px 5px #e0dcdc;

}
.navbar ul{
    display: flex;
    gap: 4px;

}
.navbar ul li{
    list-style-type: none;
    padding: 1px 15px;
}

.navbar .nav-menu a{
    text-decoration: none;
    font-size: 18px;
    color: black;
}
/* logout */
.primaryBtn{
    cursor: pointer;
    font-weight: bolder;
    padding: 13px 25px;
    font-size: 0.8rem;
    border: none;
    color: white;
    background: #db183c;
    border-radius: 15px;
    margin-left: 20px;
    transition: all 0.25s ease;
}

.primaryBtn:hover{
    transform: translateY(-5px);
    box-shadow: 0 10px 20px -10px rgba(219, 24, 44, 0.6);
}